@media screen and (max-width: 650px) {
    #search__id {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    #btn__id, #feeling-lazy {
        height: 50px;
        width: auto;
        margin: 10px auto 20px auto;
    }

    #head_title {
        font-size: 24px;
    }

    .title-wrap {
        height: 500px;
    }

    .intro-text {
        display: none;
    }

    #intro_resp {
        display: block;
    }

    #clear_box {
        font-size: 14px;
    }

}

@media screen and (max-width: 900px) {
    #resp_seal-wrap {
        width: 100px;
        height: 100px;
    }
}

@media screen and (max-width: 500px) {
    #resp_seal-wrap {
        width: 50px;
        height: 50px;
    }
}

.header-wrapper {
    width: 100%;
    background-color: #101010;
    border-radius: 0px 0px 8px 8px;
    color: white;
}

.seal-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    right: auto;
    bottom: auto;
    width: 200px;
    height: 200px;
    background-image: url(/static/media/golden-seal.ab6bab68.png);
    background-size: contain;
    z-index: 1;
}

.header-title {
    font-size: 72px;
    text-align: center;
    padding: 20px;
    backdrop-filter: blur(16px) saturate(180%);
    -webkit-backdrop-filter: blur(16px) saturate(180%);
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 12px;
    cursor: pointer;
}

.title-wrap {
    padding: 20px 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    text-align: justify;
}

.intro-text {
    width: 50%;
    text-align: center;
    padding: 20px;
    backdrop-filter: blur(16px) saturate(180%);
    -webkit-backdrop-filter: blur(16px) saturate(180%);
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 12px;
}

.intro-mob {
    display: none;
    width: 90%;
    padding: 10px;
    backdrop-filter: blur(16px) saturate(180%);
    -webkit-backdrop-filter: blur(16px) saturate(180%);
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 12px;
    text-align: center;
    margin: 0;
    font-size: 12px;
}

.folio-url {
    color: rgba(255, 0, 162);
    text-decoration: none;
    font-size: 24px;
}

.search-wrapper {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: center;
    margin-top: 20px;
    padding-bottom: 20px;
}

.search-box {
    width: 50%;
    height: 50px;
    border-radius: 5px;
    background-color: #0C0C0C;
    border: 2px ridge rgb(255, 0, 162);
    font-size: 24px;
    color: rgb(255, 0, 162);
    text-align: center;
    font-family: 'Red Hat Display', sans-serif;
    font-weight: 600;
}

.search-box:focus {
    outline-style: auto;
    outline-color: rgb(255, 0, 162);
}

.sort-btn {
    width: 50px;
    border: 3px double rgb(255, 255, 255);
    color: white;
    border-radius: 8px;
    margin: 0px 5px;
    background-color: rgb(40, 40, 40);
    font-family: 'Fira Code', sans-serif;
    font-size: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sort-btn:hover {
    border: 3px double rgb(255, 0, 162);
    background-color: rgb(80, 80, 80);
    color: rgb(222, 222, 222);
}

.btn-wrap {
    display: flex;
}

#feeling-lazy {
    width: 150px;
}
@media screen and (max-width: 650px) {
    .display-results-wrapper {
        justify-content: center;
    }

    #gen-list__id {
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }

    #gen_title {
        font-size: 24px;
        text-align: center;
    }
}

.core-wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: rgb(21, 21, 21);
    border-top-style: groove;
    border-top-width: 5px;
    border-top-color: rgb(255, 0, 162);
}

.display-genre-wrapper {
    display: flex;
    flex-direction: column;

}

.genre-list-wrapper {
    display: flex;
    flex-direction: row;
    background-color: rgb(0, 0, 0);
    overflow: scroll;
    border: 2px groove rgb(17, 0, 128);
    border-left-style: none;
    border-right-style: none;
    padding: 20px 0px;
}

.genre-title {
    font-size: 40px;
    margin-left: 20px;
}

.display-results-wrapper {
    background-color: rgb(21, 21, 21);
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    border: 2px ridge purple;
    border-left-style: none;
    border-right-style: none;
    padding: 50px 0px;
}

.random_film-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.rand_movie-box {
    margin: 25px auto 50px auto;
}

.random-film-text {
    font-size: 40px;
    font-weight: 600;
    text-align: center;
    margin: 20px auto 0px auto;
}
@media screen and (max-width: 650px) {
    #resp_box {
        width: 150px;
        height: 75px;
    }

    #resp_title {
        font-size: 32px;
    }

    #resp_director, #resp_direct-by, #resp_year {
        font-size: 14px;
    }

    #resp_runtime {
        font-size: 8px;
    }

    #resp_runtime-value {
        font-size: 14px;
    }

    #resp_details {
        flex-direction: column;
    }

    #resp_detail-box {
        width: 100%;
    }

    #resp_actor-score {
        flex-direction: column;
    }

    #resp_actor-wrap {
        width: 100%;
    }

    #resp_score-wrap {
        margin: 20px;
        width: 100%;
    }

    #resp_review-score {
        font-size: 48px;
    }

    #resp_trivia {
        width: 100%;
    }

    #resp_headline {
        overflow-wrap: break-word;
        font-size: 32px;
    }
    #resp_detail-title {
        font-size: 14px;
    }
    #resp_blurb-text {
        font-size: 16px;
    }
    #resp_trivia-text {
        font-size: 16px;
    }
    #resp_review-text {
        font-size: 16px;
    }

    
}

@media screen and (max-width: 900px) {
    #resp_detail-title {
        font-size: 10px;
    }
}

@media screen and (max-width: 750px) {
    #resp_detail-title {
        font-size: 8px;
    }
    #resp_review-wrap {
        width: 100%;
    }

}



.entire-box-wrapper {
    display: flex;
    flex-direction: column;
}

.movie-box-wrapper {
    width: 300px;
    height: 150px;
    border: 1px solid rgb(184, 86, 0);
    border-radius: 8px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    font-size: 12px;
    background-size: cover;
}

.movie-box-wrapper:hover {
    border: 4px dashed rgb(184, 86, 0);
    background-size: contain;
    background-position: center;
    transition: border 0.1s;
    transition-timing-function: ease-in;
}

#info-box-wrapper {
    width: 100%;
    height: 100%;
    display: none;
}

.mui-modal {
    width: 100%;
    height: 100%;
    display: flex;
    background-size: cover;
    /* filter: brightness(1.25); */
}

.close-modal {
    color: orangered;
    cursor: pointer;
    width: 50px;
    height: 50px;
    backdrop-filter: blur(16px) saturate(180%);
    -webkit-backdrop-filter: blur(16px) saturate(180%);
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50px;
}


.modal-wrapper {
    color: white;
    width: 90%;
    height: 85%;
    padding: 50px;
    outline: none;
    text-align: center;
    overflow: scroll;
    margin: auto;
    
    /* GLASSMORPHISM CSS */
    backdrop-filter: blur(16px) saturate(180%);
    -webkit-backdrop-filter: blur(16px) saturate(180%);
    background-color: rgba(0, 0, 0, 0.25);

    border-radius: 20px;
    border: 2px solid rgba(255,255,255, 0.5);
}

.trailer-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    margin: auto;
    border-radius: 12px;
}

.top-wrap {
    display: flex;
    width: 100%;
    align-items: center;

}

.iframe-container {
    position: relative;
    overflow: hidden;
    margin: auto;
    padding-top: 50%;
    border-radius: 12px;
}



.title-text {
    font-size: 60px;
    color: rgb(255, 0, 162);
    text-align: center;
    width: 80%;
    margin: 20px auto;
}

.det-bar-wrapper {
    width: 75%;
    height: 75px;
    margin: 50px auto 25px auto;
    border-radius: 12px;
    border: 1px solid rgba(255,255,255,0.25);
