    border-top: none;
    border-left: none;
    border-bottom: none;
    border-radius: 12px 0px 0px 12px;
}

.director-wrap {
    width: 80%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.directed-by {
    font-size: 20px;
