    color: rgba(125, 125, 125)
}

.runtime-wrap {
    width: 20%;
    height: 100%;
    display: flex;
    flex-direction: column;
    border-radius: 0px 12px 12px 0px;
    border-top: none;
    border-right: none;
    border-bottom: none;
    border: 1px solid rgba(255, 255, 255, 0.05);
    background-color: rgba(255, 255, 255, 0.05);
}

.runtime-text {
    margin: 5px 0 0 0;
}

.runtime-value {
    margin-top: 10px;
}

.details-wrapper {
