    flex-direction: row;
}

.detail-box {
    width: 20%;
    margin: 0;
}

.detail-title {
    color: red;
    font-weight: 700;
    font-size: 12px;
}

.actor_score-wrapper {
    width: 75%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 20px auto;
}

.not_act-wrap {
    display: flex;
    flex-direction: column;
    width: 40%;
    border-radius: 12px;
    background-color: rgba(255, 255, 255, 0.05);
}

.not_act-title {
    color: red;
    margin: 5px auto;
    font-size: 14px;
}

.not_act-entries {
    margin: 10px auto;
