}

.blurb-title {
    font-weight: 800;
}

.blurb-text {
    font-size: 20px;
}

.trivia-wrapper {
    width: 50%;
    background-color: rgba(255, 255, 255, 0.1);
    margin: 20px auto;
    padding: 10px;
    border-radius: 12px;
}

.trivia-title {
    color: red;
}

.trivia-text {
    font-size: 20px;
}

.review-wrapper {
    width: 75%;
    margin: 50px auto 25px auto;
    backdrop-filter: blur(16px) saturate(180%);
    -webkit-backdrop-filter: blur(16px) saturate(180%);
    background-color: rgba(255, 255, 255, 0.05);
    padding: 10px 20px;
    border-radius: 12px;
}

.review-headline {
    font-size: 48px;
    text-align: center;
}

.review-text {
    text-align: justify;
    font-size: 20px;
}

.review_score-text {
    color: rgb(255, 0, 255);
}

::-webkit-scrollbar {
    width: 10px;
    background: rgb(255, 255, 255, 0);
    border-radius: 30px;
}

::-webkit-scrollbar-thumb {
    background: rgb(225, 225, 225);
    border-radius: 15px;
}

::-webkit-scrollbar-track {
    margin: 50px 0px;
}

::-webkit-scrollbar-corner {
    display: none;
}
